const[comment , setcomment]=useState("")
const[followerss , setfellowerss]=useState("")
const[followingg , setfellowingg]=useState([])

useEffect(() => {
  const handleBeforeUnload = () => {
    window.scrollTo(0, 0);
  };

  window.addEventListener('beforeunload', handleBeforeUnload);

  return () => {
    window.removeEventListener('beforeunload', handleBeforeUnload);
  };
}, []);
const handlefetchcategortid=async()=>{
  const respose=await axios.get(`https://creen-program.com/api/categories`,{
    headers: {
      'Content-type': 'application/json; charset=UTF-8',
      'accept': 'application/json'
      ,Authorization:`Bearer ${token}`
  },
  })
setcatigories(respose.data.data)

  
}
const fetchcomment=async(post_id_comment)=>{

  try {
    if(post_id_comment  ){
      const response = await axios.post(
        `https://creen-program.com/api/comments/create?blog_id=${post_id_comment}&comment=${comment}&user_id=${user_id}`,
        {},
        {
          headers: {
            Authorization:`Bearer ${token}`,
          },
        }
      );
    
      
  
  if( response.status === 200){
    
    toast.success("created comment successfully")
    window.location.reload()
    setcomment("")
  }
  
    }
      } catch (error) {
        if (error.response) {
          console.error('Server responded with an error:', error.response.data);
          console.error('Status code:', error.response.status);
        } else if (error.request) {
          console.error('No response received from the server');
        } else {
          console.error('Error setting up the request:', error.message);
        }
      }
     
    
}
useEffect(()=>{
  const followers=async()=>{
    try{
      const response= await axios.post(`https://creen-program.com/api/followers/followerscount?user_id=${user_id}`,{},
      {
        headers:{
          Authorization:`Bearer ${token}`
        }
      })
     if(response.status === 200){
     // setfellowerss((prev)=>[...prev , ...response.data.data])
    //  console.log(followerss)
    setfellowerss(response.data.data)
      
     }
    }catch(error){}
  }
  const following=async()=>{
    try{
      const response= await axios.post(`https://creen-program.com/api/followers/followingcount?user_id=${user_id}`,{},
      {
        headers:{
          Authorization:`Bearer ${token}`
        }
      })
     if(response.status === 200){
     // setfellowerss((prev)=>[...prev , ...response.data.data])
    //  console.log(followerss)
    setfellowingg(response.data.data)
    
     }
    }catch(error){}
  }
  followers()
  following()
},[user_id])

const handlelikepost = (postId) => {
  
  setData((prevPosts) =>
    prevPosts.map((post) =>
      post.id === postId
        ? {
            ...post,
            likes_count: post.is_like ? post.likes_count - 1 : post.likes_count + 1 ,
            is_like: !post.is_like,
          }
        : post
    )
  );
  localStorage.setItem("post_id",postId)
};

const fetchdatacaption=async()=>{
const response=await axios.post(`https://creen-program.com/api/blogs/create?category_id=${category_id}&content=${caption}`,{},{
  body: JSON.stringify({}),

  headers: {
    Authorization: `Bearer ${token}`,
    
    

},})
if(response.data.status === true){
  toast.success("Post Created Successfully ")
  window.location.reload()
}
}
const fetchdataimg=async()=>{
  const response=await axios.post(`https://creen-program.com/api/blogs/create?category_id=${category_id}&content=${caption}&image=${imgUrl}`,{},{
  body: JSON.stringify({}),

  headers: {
    Authorization: `Bearer ${token}`,
    
    

},
})
if(response.data.status === true){
  toast.success("Post Created Successfully ")
}
}
const add_overlay=(e)=>{
  const add_post_click=document.querySelector(".add_post_click") 
 add_post_click.classList.add("overlay")
  add_post_click.style.display="block"
  handlefetchcategortid()
  }
  
  const remove_overlay=()=>{
    const add_post_click=document.querySelector(".add_post_click") 
    
  add_post_click.classList.remove("overlay")
  add_post_click.style.display="none"
  
  setcaption("")
  setimgUrl("")
  }
  const handlechangecaopion=(e)=>{
    if(e.target.value){
      setcaption(e.target.value)
    
      
    }else{
      setcaption("")
    }
    
    
  }
  
const handlechange=(e)=>{
  if(e.target.files[0]){
    setimg(e.target.files[0])
  }
  setimgUrl(URL.createObjectURL(e.target.files[0]))

}
const handelchangecomment=(e)=>{
  if(e.target.value){
    setcomment(e.target.value)
  }else{
    setcomment("")
  }
}
const handlechangecategoryid=(e)=>{
  if(e.target.value){
    setcategoryid(e.target.value)
    
  }else{
    setcategoryid("")
    setcatigories()
  }
}
const [stories , setstories]=useState([])
useEffect(()=>{

 const handlestory=async()=>{
  try{
    const response=await axios.post(`https://creen-program.com/api/stories`,{},{
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
        'accept': 'application/json',
        Authorization:`Bearer ${token}`
    },
  
    })
    
      
      setstories(response.data.data.stories.data)
      
  
    
    
  }catch(error){}


 }
handlestory()
},[])

const handleupload=(event)=>{
  if(caption === "" && imgUrl ===""){
    toast.error("Prevented Access to Photo or Caption Submission")
  }else{
    
    
    if(imgUrl === ''){
    console.log(category_id)
     fetchdatacaption()
    // remove_overlay()
    }else{
      fetchdataimg()
      console.log(caption , imgUrl)
     // remove_overlay()
    }
  }

}
useEffect(()=>{
  const handlepost=async()=>{
try{
  const response=await axios.post(`https://creen-program.com/api/blogs?page=${page}`,{
    headers: {
      'Content-type': 'application/json; charset=UTF-8',
      'accept': 'application/json'
  },

  })
  if(response.status === 200){
    const response=await axios.post(`https://creen-program.com/api/blogs?page=${page}`,{
      headers: {
        'Content-type': 'application/json; charset=UTF-8',
        'accept': 'application/json'
    },
  
    })
    setData((prevData) => [...prevData, ...response.data.data.data]);

  }
  
}catch(error){}

  }
  handlepost()
},[page])
const handleScroll = () => {
  if (
      window.innerHeight + document.documentElement.scrollTop + 1 >= document.documentElement.scrollHeight
  ) {
      setPage((prevPage) => prevPage + 1);
  }
};
useEffect(() => {
  window.addEventListener('scroll', handleScroll);
  return () => window.removeEventListener('scroll', handleScroll);
},[]);

