import React, { useState } from 'react'
import ReactStars from 'react-stars'
import Usericon from '../assets/images/user-icon.png'


const Trainer = () => {
    const [tab , settab]=useState('')
  return (
    <div className="trainer" style={{background:"#fff"}}>
        <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column",alignItems:"end"}}>
        <span style={{    fontSize: "24px",
        color: "rgb(102 99 99)"}}>  </span>
        <span style={{    fontSize: "24px",
        color: "rgb(102 99 99)"}}> </span>
        <ReactStars
  count={5} 
  value={5}
  size={32}
  color={'#ffd700'} />
      </div>
      <img src={Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
    </div>
    <div className="content" style={{    display: "flex",
    flexDirection: "column"}}>
        <p style={{    textAlign: "end",
    color: "#231f1f",
    height: "97px",
    overflow: "hidden"}}>                                                                     </p>
        <span style={{direction: "rtl",
    color: "green",
    marginTop: "-15px",
    cursor: "pointer"}}>...</span>
    </div>
    <div className="packges" style={{    textAlign: "center",
    marginRight: "9px",
    marginTop: "16px"}}>
        <p style={{display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    color: "#000",
    fontWeight: "bold",
    gap: "5px"}}>  <span style={{background: "red",
        padding: "10px",
        color: "white",
        borderRadius: "2px",
        marginTop: "-13px"}}></span></p>
        
        <p style={{color: "#000",
    fontWeight: "bold",
    background: "yellow",
    borderRadius: "5px",
    width: "75%",
    display: "flex",
    justifyContent: "center",
    margin: "auto"}}>  </p>
    </div>
    <div className="packges-buttons"  style={{marginTop: "10px",
    display: "flex",
    justifyContent: "space-around",
    flexDirection: "row-reverse"}}>
        <button onClick={()=>settab('')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span></span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>50</span>
        </button>
        <button onClick={()=>settab('')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span></span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>100</span>
        </button>
        <button onClick={()=>settab('')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span></span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>150</span>
        </button>
            
    </div>
    <div className="packages-content" style={{textAlign: "end",
    marginRight: "17px",
    marginTop: "7px",paddingBottom:"1px"}} >
    {
        tab === ''? <div className='excellence'>
           
        <div className="excellence-content">

            <p style={{color:"#000"}}>   </p>
            <p style={{color:"#000"}}>    </p>
            <p style={{color:"#000"}}>    </p>
            <p style={{color:"#000"}}>   </p>
            <p style={{color:"#000"}}>       </p>
            
        </div>
    
    </div>:
        tab===''?
        <div className="pro-content">

            <p style={{color:"#000"}}>    </p>
            <p style={{color:"#000"}}>      </p>

            <p style={{color:"#000"}}>  </p>
  
            
        </div>:
        tab===''?
        <div className="master-content">
            <p style={{color:"#000"}}>    </p>
            <p style={{color:"#000"}}>      </p>
            <p style={{color:"#000"}}>   </p>
            <p style={{color:"#000"}}>   </p>
            
        </div>:<></>
    }
       
        <div></div>
        <div></div>
    </div>
    <div className="subscriptionall" style={{display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",justifyContent: "center",
    padding: "10px",
    background: "bisque",
    borderTopRightRadius: "10px",
    borderTopLeftRadius: "10px",
    padding: "22px 0px 77px 0px"
}}>
        <div className="buttons-all" style={{    display: "flex",
    flexDirection: "column",
    alignItems: "end"}}>
        <div className="one" style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px",
    width: "100%",
    background: "#fff",
    borderRadius: "10px",
    padding: "10px 10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: "red",
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:"#fff"}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto",    fontWeight: "bold"}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>150<span>sar</span></span><span>  </span></p>
        </div>
        
        <div className="two" style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px", padding: "10px 10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: "red",
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:"#fff"}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto"}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>100 <span>sar</span></span><span> </span></p>
        </div>
        
        <div className="three" style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px", padding: "10px 10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: "red",
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:"#fff"}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto"}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>50<span>sar</span></span><span> </span></p>
        </div>
        </div>
        <button style={{    background: "red",
    border: "none",
    width: "80%",
    padding: "10px",
    borderRadius: "25px",
    color: "#fff"}}></button>
    </div>
    </div>
  )
}

export default Trainer